.aboutMe {
 height: 100vh;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center;
 background-image: linear-gradient(to bottom, #F3E99F, #FFF2CC);
}

.aboutMe h1.title {
 font-size: 6vmin;
}

.aboutMe-text {
 width: 80%;
 margin: 2vmin, auto;
}


.skills{
 margin-top: 10vmin;
}

.skills-icons {
 justify-content: center;
 align-items: center;
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;
}

.skills h2 {
 display: inline-block;
}

i {
 font-size: 6vmin;
 margin: 1vmin;
}