.contact {
 height: 100vh;
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;
 justify-content: center;
 align-items: center;
 background-image: linear-gradient(to bottom, #FBF1D3, #FFF9DE);
}

.contact h1 {
 font-size: 10vmin;
}

.contact a {
 color: black;
 text-decoration: none;
}

.contact a:hover, nav a:visited, nav a:link, nav a:active {
 text-decoration: none;
 color: black;
}

.contact i {
 font-size: 30vmin;
}